//

let _tool = {
	//
	strLeng(str) {
		if(str) {
			let len = str.length, truelen = 0;
			for(let x = 0; x < len; x++) {
				if(str.charCodeAt(x) > 128){
					truelen += 2;
				}else{
					truelen += 1;
				}
			}
			return truelen;
		} else {
			return 0;
		}
	},
	//
	getPageNum(total,row) {
		let num = Number(total) / Number(row);
		//
		if(num%1 !== 0) {
			let b = num.toString();	//
			let a = parseInt(b.substring(0,b.indexOf('.')));	//
			let s = b.replace(/\d+\.(\d*)/, '$1');	//
			if(s > 0) {
				num = a + 1;
			}
		}
		return num;
	},
	//
	setBarColor(black = false) {
		if(black) {
			uni.setNavigationBarColor({
			    frontColor: '#000000',
			    backgroundColor: '#FAFAFA'
			});
		} else {
			uni.setNavigationBarColor({
			    frontColor: '#ffffff',
			    backgroundColor: '#FAFAFA'
			});
		}
	},
	zaiui_log(v) {
		console.error("!");
		console.warn(",HBX" + v);
		console.info("QQ707134214");
	}
};

export default _tool;